.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

header {
  background-color: cornflowerblue;
  padding: 2rem;
  margin: 0;
}

main {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
}

.optionsContainer{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
}

.stateSetter{
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}



